"use client"

import type React from "react"
import { useEffect, useState, useCallback } from "react"
import { useRouter } from "next/navigation"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Card, CardContent, CardHeader } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import {
  Edit2,
  Users,
  Search,
  AlertCircle,
  CheckCircle2,
  Clock,
  XCircle,
  CalendarClock,
  Trash2,
  Calendar,
  MapPin,
  Plus,
  X,
  Filter,
  Download,
} from "lucide-react"
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Skeleton } from "@/components/ui/skeleton"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"
import { DropdownMenu, DropdownMenuContent, DropdownMenuTrigger } from "@/components/ui/dropdown-menu"
import { useToast } from "@/components/ui/use-toast"
// Import ExcelJS dynamically to avoid SSR issues
const ExcelJS = { Workbook: null }

interface Soutenance {
  idSoutenance: number
  date: string
  time: string
  location: string
  groupNames: string[]
  juryNames: string[]
  status: string
}

interface StatusConfig {
  color: string
  bg: string
  hoverBg: string
  icon: React.ReactNode
  text: string
}

interface Salle {
  idSalle: string
  nomSalle: string
}

interface Group {
  idGroupe: string
  nomGroupe: string
  nbEtudiants: number
  students: string[] // Added to store student names
}

function getStatusConfig(status: string): StatusConfig {
  switch (status) {
    case "Scheduled":
      return {
        color: "text-blue-700 dark:text-blue-300",
        bg: "bg-blue-100 dark:bg-blue-900/50",
        hoverBg: "hover:bg-blue-200 dark:hover:bg-blue-900/70",
        icon: <CalendarClock className="w-3.5 h-3.5" />,
        text: "Planifié",
      }
    case "Completed":
      return {
        color: "text-green-700 dark:text-green-300",
        bg: "bg-green-100 dark:bg-green-900/50",
        hoverBg: "hover:bg-green-200 dark:hover:bg-green-900/70",
        icon: <CheckCircle2 className="w-3.5 h-3.5" />,
        text: "Terminé",
      }
    case "Pending":
      return {
        color: "text-yellow-700 dark:text-yellow-300",
        bg: "bg-yellow-100 dark:bg-yellow-900/50",
        hoverBg: "hover:bg-yellow-200 dark:hover:bg-yellow-900/70",
        icon: <Clock className="w-3.5 h-3.5" />,
        text: "En attente",
      }
    case "Cancelled":
      return {
        color: "text-red-700 dark:text-red-300",
        bg: "bg-red-100 dark:bg-red-900/50",
        hoverBg: "hover:bg-red-200 dark:hover:bg-red-900/70",
        icon: <XCircle className="w-3.5 h-3.5" />,
        text: "Annulé",
      }
    default:
      return {
        color: "text-gray-700 dark:text-gray-300",
        bg: "bg-gray-100 dark:bg-gray-800",
        hoverBg: "hover:bg-gray-200 dark:hover:bg-gray-800/70",
        icon: <AlertCircle className="w-3.5 h-3.5" />,
        text: "Inconnu",
      }
  }
}

function StatusBadge({ status }: { status: string }) {
  const config = getStatusConfig(status)

  return (
    <Badge
      variant="outline"
      className={`
        ${config.bg} 
        ${config.color} 
        ${config.hoverBg}
        border-0 
        font-medium 
        gap-1.5
        px-2.5 
        py-0.5 
        transition-colors
        duration-200
        inline-flex
        items-center
        cursor-default
        select-none
        shadow-sm
      `}
    >
      {config.icon}
      {config.text}
    </Badge>
  )
}

function DeleteDialog({
  onDelete,
  idSoutenance,
}: {
  onDelete: () => void
  idSoutenance: number
}) {
  return (
    <AlertDialog>
      <AlertDialogTrigger asChild>
        <Button variant="ghost" size="icon" className="hover:bg-red-100 hover:text-red-600 dark:hover:bg-red-900/30">
          <Trash2 className="h-4 w-4" />
          <span className="sr-only">Supprimer la soutenance</span>
        </Button>
      </AlertDialogTrigger>
      <AlertDialogContent className="max-w-md">
        <AlertDialogHeader>
          <AlertDialogTitle>Confirmer la suppression</AlertDialogTitle>
          <AlertDialogDescription>
            Êtes-vous sûr de vouloir supprimer cette soutenance ? Cette action est irréversible.
          </AlertDialogDescription>
        </AlertDialogHeader>
        <AlertDialogFooter>
          <AlertDialogCancel>Annuler</AlertDialogCancel>
          <AlertDialogAction onClick={onDelete} className="bg-red-600 hover:bg-red-700 dark:hover:bg-red-700">
            Supprimer
          </AlertDialogAction>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>
  )
}

export default function SoutenancesPage() {
  const [soutenances, setSoutenances] = useState<Soutenance[]>([])
  const [filteredSoutenances, setFilteredSoutenances] = useState<Soutenance[]>([])
  const [searchTerm, setSearchTerm] = useState("")
  const [groups, setGroups] = useState<Group[]>([])
  const [salles, setSalles] = useState<Salle[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [statusFilter, setStatusFilter] = useState<string>("all")
  const [activeTab, setActiveTab] = useState("all")
  const currentYear = new Date().getFullYear().toString()
  const [yearFilter, setYearFilter] = useState(currentYear)
  const [locationFilter, setLocationFilter] = useState("all")
  const [juryFilter, setJuryFilter] = useState("all")
  const [isGeneratingExcel, setIsGeneratingExcel] = useState(false)
  const { toast } = useToast()

  // Sample data for rooms
  const rooms: Salle[] = [
    { idSalle: "1", nomSalle: "Salle 1" },
    { idSalle: "2", nomSalle: "Salle 2" },
    { idSalle: "3", nomSalle: "Salle 3" },
    { idSalle: "4", nomSalle: "Salle 4" },
    { idSalle: "5", nomSalle: "Salle 5" },
    { idSalle: "6", nomSalle: "Salle 6" },
  ]

  // Sample data for jury members
  const juryMembers = ["Prof. Martin", "Dr. Dubois", "Prof. Laurent", "Dr. Bernard", "Prof. Moreau", "Dr. Petit"]

  const router = useRouter()

  const fetchSoutenances = useCallback(async () => {
    try {
      setIsLoading(true)
      const response = await fetch("http://localhost:5000/api/soutenance")
      if (!response.ok) {
        throw new Error("Erreur lors du chargement des soutenances")
      }
      const data = await response.json()
      const normalizedData = data.map((s: Soutenance) => ({
        ...s,
        groupNames: Array.isArray(s.groupNames) ? s.groupNames : s.groupNames ? [s.groupNames] : [],
        juryNames: Array.isArray(s.juryNames) ? s.juryNames : s.juryNames ? [s.juryNames] : [],
      }))
      setSoutenances(normalizedData)
    } catch (error) {
      console.error("Error fetching soutenances:", error)
      setError("Impossible de charger les soutenances. Veuillez réessayer plus tard.")
    } finally {
      setIsLoading(false)
    }
  }, [])

  const fetchGroupsAndSalles = useCallback(async () => {
    try {
      const groupsResponse = await fetch("http://localhost:5000/api/groups")
      if (!groupsResponse.ok) {
        throw new Error("Failed to fetch groups")
      }
      const groupsData = await groupsResponse.json()
      setGroups(groupsData) // Expecting { idGroupe, nomGroupe, nbEtudiants, students }
      setSalles(rooms)
    } catch (error) {
      console.error("Error fetching data:", error)
    }
  }, [])

  useEffect(() => {
    fetchSoutenances()
    fetchGroupsAndSalles()
  }, [fetchSoutenances, fetchGroupsAndSalles])

  useEffect(() => {
    applyFilters()
  }, [soutenances, searchTerm, statusFilter, yearFilter, locationFilter, juryFilter])

  useEffect(() => {
    async function loadExcelJS() {
      const exceljs = await import("exceljs")
      ExcelJS.Workbook = exceljs.Workbook
    }
    loadExcelJS()
  }, [])

  const applyFilters = () => {
    let filtered = [...soutenances]
    if (searchTerm) {
      filtered = filtered.filter(
        (s) =>
          s.groupNames.some((group) => group.toLowerCase().includes(searchTerm.toLowerCase())) ||
          s.juryNames.some((jury) => jury.toLowerCase().includes(searchTerm.toLowerCase())) ||
          s.location.toLowerCase().includes(searchTerm.toLowerCase()),
      )
    }
    if (statusFilter !== "all") {
      filtered = filtered.filter((s) => s.status === statusFilter)
    }
    if (yearFilter !== "all") {
      filtered = filtered.filter((s) => new Date(s.date).getFullYear().toString() === yearFilter)
    }
    if (locationFilter !== "all") {
      filtered = filtered.filter((s) => s.location === locationFilter)
    }
    if (juryFilter !== "all") {
      filtered = filtered.filter((s) => s.juryNames.some((jury) => jury === juryFilter))
    }
    setFilteredSoutenances(filtered)
  }

  const resetFilters = () => {
    setSearchTerm("")
    setStatusFilter("all")
    setActiveTab("all")
    setYearFilter(currentYear)
    setLocationFilter("all")
    setJuryFilter("all")
  }

  const handleEdit = (soutenance: Soutenance) => {
    router.push(`/pages/edit_soutenance/${soutenance.idSoutenance}`)
  }

  const handleDelete = async (id: number) => {
    if (!id) {
      setError("Données de soutenance manquantes")
      return
    }
    try {
      const response = await fetch(`http://localhost:5000/api/soutenance/${id}`, { method: "DELETE" })
      if (!response.ok) {
        const errorData = await response.text()
        throw new Error(`Erreur serveur: ${errorData}`)
      }
      await fetchSoutenances()
      console.log("✅ Soutenance supprimée avec succès!")
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : "Une erreur inconnue est survenue"
      console.error("❌ Erreur de suppression:", errorMessage)
      setError(`Échec de la suppression de la soutenance: ${errorMessage}`)
    }
  }

  const handleAddNew = () => {
    router.push("/pages/PlanifierSoutenance")
  }

  const getTabCount = (status: string) => {
    if (status === "all") return soutenances.length
    return soutenances.filter((s) => s.status === status).length
  }

  // Format date for Excel
  const formatDate = (dateString: string): string => {
    const date = new Date(dateString)
    return date
      .toLocaleDateString("fr-FR", {
        day: "2-digit",
        month: "2-digit",
        year: "numeric",
      })
      .replace(/\//g, "/") // Ensure proper date format
  }

  // Group soutenances by jury
  const groupSoutenancesByJury = (soutenances: Soutenance[]) => {
    const juryGroups: { [key: string]: Soutenance[] } = {}

    soutenances.forEach((soutenance) => {
      const juryKey = soutenance.juryNames.join(", ")
      if (!juryGroups[juryKey]) {
        juryGroups[juryKey] = []
      }
      juryGroups[juryKey].push(soutenance)
    })

    return juryGroups
  }

  // Generate Excel file that matches the screenshot
  const generateExcelFile = async () => {
    try {
      if (!ExcelJS.Workbook) {
        toast({
          title: "Erreur",
          description: "La bibliothèque Excel n'est pas encore chargée. Veuillez réessayer.",
          variant: "destructive",
        })
        return
      }

      setIsGeneratingExcel(true)

      // Create a new workbook and worksheet
      const workbook = new ExcelJS.Workbook()
      const worksheet = workbook.addWorksheet("Planning des Soutenances")

      // --- Title Section ---
      worksheet.mergeCells("A1:E1")
      worksheet.getCell("A1").value = "Université Cadi Ayyad"
      worksheet.getCell("A1").font = { size: 14, bold: true }
      worksheet.getCell("A1").alignment = { horizontal: "center" }

      worksheet.mergeCells("A2:D2")
      worksheet.getCell("A2").value = "Ecole Supérieure de Technologie -Safi-"
      worksheet.getCell("A2").font = { size: 12, bold: true }
      worksheet.getCell("A2").alignment = { horizontal: "center" }
      worksheet.getCell("E2").value = `A.U: ${new Date().getFullYear() - 1}-${new Date().getFullYear()}`
      worksheet.getCell("E2").font = { italic: true }
      worksheet.getCell("E2").alignment = { horizontal: "right" }

      worksheet.getCell("A3").value = "Département: Informatique"
      worksheet.getCell("A3").font = { bold: true }

      worksheet.getCell("A4").value = "Niveau: 2ème Année"
      worksheet.getCell("A4").font = { bold: true }

      worksheet.mergeCells("A5:E5")
      worksheet.getCell("A5").value = "Planning des Soutenances (PFE)"
      worksheet.getCell("A5").font = { size: 14, bold: true }
      worksheet.getCell("A5").alignment = { horizontal: "center" }

      // Add some space
      worksheet.addRow([])

      // Start the table at row 7
      const tableStartRow = 7

      // Set column widths
      worksheet.getColumn("A").width = 20 // Jury
      worksheet.getColumn("B").width = 20 // Jours
      worksheet.getColumn("C").width = 10 // N° (changed back to 10 to match screenshot)
      worksheet.getColumn("D").width = 40 // Nom
      worksheet.getColumn("E").width = 15 // Lieu

      // Add and style the header row
      const headerRow = worksheet.addRow(["Jury", "Jours", "N°", "Nom", "Lieu"])
      headerRow.height = 20 // Set header row height
      headerRow.eachCell((cell) => {
        cell.fill = {
          type: "pattern",
          pattern: "solid",
          fgColor: { argb: "9FC5E8" }, // Light blue color as in screenshot
        }
        cell.border = {
          top: { style: "thin" },
          left: { style: "thin" },
          bottom: { style: "thin" },
          right: { style: "thin" },
        }
        cell.font = {
          bold: true,
          size: 12,
        }
        cell.alignment = {
          vertical: "middle",
          horizontal: "center",
        }
      })

      // Group soutenances by jury
      const juryGroups = groupSoutenancesByJury(filteredSoutenances)

      // Current row index
      let rowIndex = tableStartRow + 1

      // Process each jury group
      Object.entries(juryGroups).forEach(([juryNames, soutenances]) => {
        // Format jury names more nicely
        const formattedJuryNames = juryNames
          .split(", ")
          .map((name) => name.trim())
          .join("\n")

        // Group soutenances by date
        const dateGroups: { [key: string]: Soutenance[] } = {}
        soutenances.forEach((soutenance) => {
          const dateTime = `${formatDate(soutenance.date)} à ${soutenance.time.replace(/-/g, "-")}`
          if (!dateGroups[dateTime]) {
            dateGroups[dateTime] = []
          }
          dateGroups[dateTime].push(soutenance)
        })

        // Calculate total rows for this jury (based on actual groups)
        let totalRows = 0
        Object.entries(dateGroups).forEach(([dateKey, dateSoutenances]) => {
          const groupsCount = dateSoutenances.reduce((count, s) => count + s.groupNames.length, 0)
          totalRows += Math.max(groupsCount, 1) // At least 1 row per date
        })

        // Merge jury cell
        if (totalRows > 0) {
          worksheet.mergeCells(rowIndex, 1, rowIndex + totalRows - 1, 1)
          const juryCell = worksheet.getCell(`A${rowIndex}`)
          juryCell.value = formattedJuryNames
          juryCell.alignment = {
            vertical: "middle",
            horizontal: "center",
            wrapText: true,
          }
          juryCell.border = {
            top: { style: "thin" },
            left: { style: "thin" },
            bottom: { style: "thin" },
            right: { style: "thin" },
          }

          // Merge lieu cell (using the location from the first soutenance)
          worksheet.mergeCells(rowIndex, 5, rowIndex + totalRows - 1, 5)
          const lieuCell = worksheet.getCell(`E${rowIndex}`)
          lieuCell.value = soutenances[0].location
          lieuCell.alignment = {
            vertical: "middle",
            horizontal: "center",
            wrapText: true,
          }
          lieuCell.border = {
            top: { style: "thin" },
            left: { style: "thin" },
            bottom: { style: "thin" },
            right: { style: "thin" },
          }
        }

        // Process each date group
        Object.entries(dateGroups).forEach(([dateTime, dateSoutenances]) => {
          // Get all groups for this date and jury
          const groupsForDate = dateSoutenances.flatMap((s) =>
            s.groupNames.map((groupId) => {
              const group = groups.find((g) => g.idGroupe === groupId)
              return {
                groupId,
                groupNumber: group ? group.nomGroupe : groupId,
                students: group ? group.students : [],
              }
            }),
          )

          // Sort groups by number
          groupsForDate.sort((a, b) => {
            const numA = Number.parseInt(a.groupNumber.replace(/\D/g, "")) || 0
            const numB = Number.parseInt(b.groupNumber.replace(/\D/g, "")) || 0
            return numA - numB
          })

          // Calculate how many rows we need for this date (one row per group)
          const rowsForDate = groupsForDate.length || 1 // At least one row

          // Merge date cell for all groups in this date
          worksheet.mergeCells(rowIndex, 2, rowIndex + rowsForDate - 1, 2)
          const dateCell = worksheet.getCell(`B${rowIndex}`)
          dateCell.value = dateTime
          dateCell.alignment = {
            vertical: "middle",
            horizontal: "center",
            wrapText: true,
          }
          dateCell.border = {
            top: { style: "thin" },
            left: { style: "thin" },
            bottom: { style: "thin" },
            right: { style: "thin" },
          }

          // Add rows for each group with group names and student names
          if (groupsForDate.length === 0) {
            // If no groups, add an empty row
            const numCell = worksheet.getCell(`C${rowIndex}`)
            numCell.border = {
              top: { style: "thin" },
              left: { style: "thin" },
              bottom: { style: "thin" },
              right: { style: "thin" },
            }

            const nomCell = worksheet.getCell(`D${rowIndex}`)
            nomCell.border = {
              top: { style: "thin" },
              left: { style: "thin" },
              bottom: { style: "thin" },
              right: { style: "thin" },
            }

            rowIndex++
          } else {
            // Add each group in a separate row with sequential numbers
            groupsForDate.forEach((group, i) => {
              // Create a row for this group
              const row = worksheet.getRow(rowIndex)

              // Set row height to match screenshot
              row.height = 18 // Set consistent row height

              // Add sequential number in N° column with styling
              const numCell = row.getCell(3) // Column C
              numCell.value = i + 1 // Sequential number (1, 2, 3, etc.)
              numCell.alignment = {
                vertical: "middle",
                horizontal: "center",
              }
              numCell.border = {
                top: { style: "thin" },
                left: { style: "thin" },
                bottom: { style: "thin" },
                right: { style: "thin" },
              }

              // Add student names in Nom column with styling
              const nomCell = row.getCell(4) // Column D
              nomCell.value = group.students.length > 0 ? group.students.join(", ") : ""
              nomCell.alignment = {
                vertical: "middle",
                horizontal: "left",
                wrapText: true,
              }
              nomCell.border = {
                top: { style: "thin" },
                left: { style: "thin" },
                bottom: { style: "thin" },
                right: { style: "thin" },
              }

              rowIndex++
            })
          }
        })
      })

      // Generate the Excel file
      const buffer = await workbook.xlsx.writeBuffer()

      // Create a Blob from the buffer
      const blob = new Blob([buffer], {
        type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
      })

      // Create a download link and trigger the download
      const url = window.URL.createObjectURL(blob)
      const link = document.createElement("a")
      link.href = url
      link.download = `planning_soutenances_${new Date().toISOString().split("T")[0]}.xlsx`
      document.body.appendChild(link)
      link.click()

      // Clean up
      window.URL.revokeObjectURL(url)
      document.body.removeChild(link)

      toast({
        title: "Téléchargement réussi",
        description: "Le fichier Excel a été généré avec succès.",
        variant: "default",
      })
    } catch (error) {
      console.error("Error generating Excel file:", error)
      toast({
        title: "Erreur",
        description: "Une erreur s'est produite lors de la génération du fichier Excel.",
        variant: "destructive",
      })
    } finally {
      setIsGeneratingExcel(false)
    }
  }

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-background to-muted/20 py-12 px-4 sm:px-6">
        <div className="max-w-7xl mx-auto">
          <div className="flex justify-between items-center mb-8">
            <div>
              <h1 className="text-4xl font-bold bg-gradient-to-r from-primary to-primary/70 bg-clip-text text-transparent">
                Soutenances
              </h1>
              <p className="text-muted-foreground mt-2">Gestion des sessions de soutenance</p>
            </div>
            <Skeleton className="h-10 w-32" />
          </div>
          <Card className="w-full border-primary/10 shadow-md">
            <CardHeader className="pb-2">
              <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
                <Skeleton className="h-8 w-48" />
                <Skeleton className="h-10 w-full sm:w-96" />
              </div>
            </CardHeader>
            <CardContent className="p-0">
              <div className="p-4">
                <Skeleton className="h-8 w-full mb-4" />
                {[1, 2, 3, 4, 5].map((i) => (
                  <Skeleton key={i} className="h-16 w-full mb-2" />
                ))}
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-background to-muted/20 py-12 px-4 sm:px-6">
        <div className="max-w-7xl mx-auto">
          <Card className="w-full border-primary/10 shadow-md">
            <CardContent className="pt-6">
              <Alert variant="destructive">
                <AlertCircle className="h-4 w-4" />
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-background to-muted/20 py-12 px-4 sm:px-6">
      <div className="max-w-7xl mx-auto">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-8 gap-4">
          <div>
            <h1 className="text-4xl font-bold bg-gradient-to-r from-primary to-primary/70 bg-clip-text text-transparent">
              Soutenances
            </h1>
            <p className="text-muted-foreground mt-2">Gestion des sessions de soutenance</p>
          </div>
          <div className="flex gap-2">
            <Button
              onClick={handleAddNew}
              className="bg-primary hover:bg-primary/90 text-white rounded-full px-4 py-2 flex items-center gap-2"
            >
              <Plus className="h-5 w-5" />
              Nouvelle soutenance
            </Button>
            <Button
              onClick={generateExcelFile}
              disabled={isGeneratingExcel}
              className="bg-secondary hover:bg-secondary/90 text-secondary-foreground rounded-full px-4 py-2 flex items-center gap-2"
            >
              <Download className="h-5 w-5" />
              {isGeneratingExcel ? "Génération..." : "Télécharger Excel"}
            </Button>
          </div>
        </div>

        {soutenances.length === 0 ? (
          <Card className="w-full border-primary/10 shadow-md">
            <CardContent className="pt-6 flex flex-col items-center justify-center py-12">
              <div className="rounded-full bg-primary/10 p-4 mb-4">
                <Calendar className="h-10 w-10 text-primary" />
              </div>
              <h3 className="text-xl font-semibold mb-2">Aucune soutenance disponible</h3>
              <p className="text-muted-foreground text-center max-w-md mb-6">
                Il n'y a actuellement aucune soutenance planifiée. Commencez par en créer une nouvelle.
              </p>
              <Button onClick={handleAddNew} className="bg-primary hover:bg-primary/90 text-white">
                <Plus className="h-4 w-4 mr-2" />
                Planifier une soutenance
              </Button>
            </CardContent>
          </Card>
        ) : (
          <Card className="w-full border-primary/10 shadow-md">
            <CardHeader className="pb-2 border-b">
              <div className="flex flex-col gap-4">
                <Tabs
                  defaultValue="all"
                  value={activeTab}
                  onValueChange={(value) => {
                    setActiveTab(value)
                    setStatusFilter(value === "all" ? "all" : value)
                  }}
                  className="w-full"
                >
                  <TabsList className="grid grid-cols-5 w-full">
                    <TabsTrigger value="all" className="text-sm">
                      Toutes ({getTabCount("all")})
                    </TabsTrigger>
                    <TabsTrigger value="Pending" className="text-sm">
                      En attente ({getTabCount("Pending")})
                    </TabsTrigger>
                    <TabsTrigger value="Scheduled" className="text-sm">
                      Planifiées ({getTabCount("Scheduled")})
                    </TabsTrigger>
                    <TabsTrigger value="Completed" className="text-sm">
                      Terminées ({getTabCount("Completed")})
                    </TabsTrigger>
                    <TabsTrigger value="Cancelled" className="text-sm">
                      Annulé ({getTabCount("Cancelled")})
                    </TabsTrigger>
                  </TabsList>
                </Tabs>
                <div className="flex flex-col sm:flex-row gap-4 items-start sm:items-center justify-between">
                  <div className="relative w-full sm:w-96">
                    <Search className="absolute left-3 top-2.5 h-5 w-5 text-muted-foreground" />
                    <Input
                      placeholder="Rechercher par groupe, jury ou salle..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                      className="pl-10 pr-10 py-2 text-base rounded-full border-primary/20 focus-visible:ring-primary"
                    />
                    {searchTerm && (
                      <Button
                        variant="ghost"
                        size="icon"
                        className="absolute right-1 top-1 h-8 w-8 text-muted-foreground hover:text-foreground"
                        onClick={() => setSearchTerm("")}
                      >
                        <X className="h-4 w-4" />
                        <span className="sr-only">Effacer la recherche</span>
                      </Button>
                    )}
                  </div>
                  <div className="flex gap-2 items-center w-full sm:w-auto">
                    <DropdownMenu>
                      <DropdownMenuTrigger asChild>
                        <Button variant="outline" className="gap-2">
                          <Filter className="h-4 w-4" />
                          Filtres
                        </Button>
                      </DropdownMenuTrigger>
                      <DropdownMenuContent className="w-56 p-4">
                        <div className="flex flex-col gap-4">
                          <div className="space-y-2">
                            <label className="text-sm font-medium">Année</label>
                            <Select value={yearFilter} onValueChange={setYearFilter}>
                              <SelectTrigger>
                                <SelectValue placeholder="Toutes les années" />
                              </SelectTrigger>
                              <SelectContent>
                                <SelectItem value="all">Toutes les années</SelectItem>
                                <SelectItem value="2023">2023</SelectItem>
                                <SelectItem value="2024">2024</SelectItem>
                                <SelectItem value="2025">2025</SelectItem>
                              </SelectContent>
                            </Select>
                          </div>
                          <div className="space-y-2">
                            <label className="text-sm font-medium">Salle</label>
                            <Select value={locationFilter} onValueChange={setLocationFilter}>
                              <SelectTrigger>
                                <SelectValue placeholder="Toutes les salles" />
                              </SelectTrigger>
                              <SelectContent>
                                <SelectItem value="all">Toutes les salles</SelectItem>
                                {rooms.map((room) => (
                                  <SelectItem key={room.idSalle} value={room.nomSalle}>
                                    {room.nomSalle}
                                  </SelectItem>
                                ))}
                              </SelectContent>
                            </Select>
                          </div>
                          <div className="space-y-2">
                            <label className="text-sm font-medium">Jury</label>
                            <Select value={juryFilter} onValueChange={setJuryFilter}>
                              <SelectTrigger>
                                <SelectValue placeholder="Tous les jurys" />
                              </SelectTrigger>
                              <SelectContent>
                                <SelectItem value="all">Tous les jurys</SelectItem>
                                {juryMembers.map((jury) => (
                                  <SelectItem key={jury} value={jury}>
                                    {jury}
                                  </SelectItem>
                                ))}
                              </SelectContent>
                            </Select>
                          </div>
                          <Button variant="outline" onClick={resetFilters} className="mt-2">
                            Réinitialiser les filtres
                          </Button>
                        </div>
                      </DropdownMenuContent>
                    </DropdownMenu>
                  </div>
                </div>
              </div>
            </CardHeader>
            <CardContent className="p-0">
              <div className="relative w-full overflow-auto">
                <Table>
                  <TableHeader className="bg-muted/30">
                    <TableRow>
                      <TableHead className="w-[200px] font-semibold">
                        <div className="flex items-center gap-1">
                          <Calendar className="h-4 w-4 text-primary" />
                          Date
                        </div>
                      </TableHead>
                      <TableHead className="w-[100px] font-semibold">
                        <div className="flex items-center gap-1">
                          <Clock className="h-4 w-4 text-primary" />
                          Heure
                        </div>
                      </TableHead>
                      <TableHead className="w-[150px] font-semibold">
                        <div className="flex items-center gap-1">
                          <MapPin className="h-4 w-4 text-primary" />
                          Lieu
                        </div>
                      </TableHead>
                      <TableHead className="w-[250px] font-semibold">
                        <div className="flex items-center gap-1">
                          <Users className="h-4 w-4 text-primary" />
                          Groupes
                        </div>
                      </TableHead>
                      <TableHead className="w-[250px] font-semibold">Jury</TableHead>
                      <TableHead className="w-[120px] font-semibold">Statut</TableHead>
                      <TableHead className="w-[100px] font-semibold text-right">Actions</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {filteredSoutenances.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={7} className="text-center py-8">
                          <div className="flex flex-col items-center justify-center">
                            <Search className="h-12 w-12 text-muted-foreground mb-4" />
                            <h3 className="text-lg font-medium mb-2">Aucune soutenance trouvée</h3>
                            <p className="text-muted-foreground">
                              Essayez d'ajuster vos filtres ou de créer une nouvelle soutenance.
                            </p>
                          </div>
                        </TableCell>
                      </TableRow>
                    ) : (
                      filteredSoutenances.map((soutenance, index) => {
                        const soutenanceYear = new Date(soutenance.date).getFullYear().toString()
                        const isCurrentYear = soutenanceYear === currentYear
                        return (
                          <TableRow
                            key={soutenance.idSoutenance ?? `soutenance-${index}`}
                            className="transition-colors hover:bg-muted/50"
                          >
                            <TableCell className="font-medium">
                              {new Date(soutenance.date).toLocaleDateString("fr-FR", {
                                day: "numeric",
                                month: "long",
                                year: "numeric",
                              })}
                            </TableCell>
                            <TableCell>{soutenance.time}</TableCell>
                            <TableCell>
                              <div className="flex items-center gap-1">
                                <MapPin className="h-3.5 w-3.5 text-muted-foreground" />
                                {soutenance.location}
                              </div>
                            </TableCell>
                            <TableCell>
                              <div className="space-y-1">
                                {soutenance.groupNames.map((groupId, idx) => {
                                  const group = groups.find((g) => g.idGroupe === groupId)
                                  const students = group ? group.students.join(", ") : "N/A"
                                  return (
                                    <div key={idx} className="text-sm">
                                      <Badge
                                        variant="secondary"
                                        className="mr-2 bg-primary/10 text-primary hover:bg-primary/20"
                                      >
                                        {group ? group.nomGroupe : groupId}
                                      </Badge>
                                      {students}
                                    </div>
                                  )
                                })}
                              </div>
                            </TableCell>
                            <TableCell>
                              <TooltipProvider>
                                <Tooltip>
                                  <TooltipTrigger asChild>
                                    <div className="flex items-center gap-2 cursor-pointer">
                                      <Users className="h-4 w-4 text-muted-foreground" />
                                      <span className="truncate max-w-[200px]">{soutenance.juryNames.join(", ")}</span>
                                    </div>
                                  </TooltipTrigger>
                                  <TooltipContent>
                                    <p className="max-w-xs">{soutenance.juryNames.join(", ")}</p>
                                  </TooltipContent>
                                </Tooltip>
                              </TooltipProvider>
                            </TableCell>
                            <TableCell>
                              <StatusBadge status={soutenance.status} />
                            </TableCell>
                            <TableCell className="text-right">
                              <div className="flex items-center justify-end gap-1">
                                {isCurrentYear ? (
                                  <>
                                    <TooltipProvider>
                                      <Tooltip>
                                        <TooltipTrigger asChild>
                                          <Button
                                            variant="ghost"
                                            size="icon"
                                            className="h-8 w-8 hover:bg-primary/10 hover:text-primary"
                                            onClick={() => handleEdit(soutenance)}
                                          >
                                            <Edit2 className="h-4 w-4" />
                                            <span className="sr-only">Modifier la soutenance</span>
                                          </Button>
                                        </TooltipTrigger>
                                        <TooltipContent>
                                          <p>Modifier la soutenance</p>
                                        </TooltipContent>
                                      </Tooltip>
                                    </TooltipProvider>
                                    <TooltipProvider>
                                      <Tooltip>
                                        <TooltipTrigger asChild>
                                          <DeleteDialog
                                            onDelete={() => handleDelete(soutenance.idSoutenance)}
                                            idSoutenance={soutenance.idSoutenance}
                                          />
                                        </TooltipTrigger>
                                        <TooltipContent>
                                          <p>Supprimer la soutenance</p>
                                        </TooltipContent>
                                      </Tooltip>
                                    </TooltipProvider>
                                  </>
                                ) : (
                                  <span className="text-sm text-muted-foreground"></span>
                                )}
                              </div>
                            </TableCell>
                          </TableRow>
                        )
                      })
                    )}
                  </TableBody>
                </Table>
              </div>
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  )
}

